import React from "react";
import s from "./Post.module.scss";
import IconButton from "@mui/material/IconButton";
import ThumbUpIcon from "@mui/icons-material/ThumbUp";
import StarOutline from "@mui/icons-material/StarSharp";
import Button from "@mui/material/Button";
import { Link, useParams } from "react-router-dom";
import { useGetCommentsQuery, useGetPostsQuery } from "../../redux"; //     Redux store
import { AddComment } from "../../components/AddComment";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import GradeIcon from "@mui/icons-material/Grade";
import { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import {
  addToFavorites,
  removeFromFavorites,
} from "../../redux/favoritesSlice";

export const Post = () => {
  //   ID  ROUTER
  const { postId } = useParams();

  //       
  const { data: posts } = useGetPostsQuery();
  const post = posts?.find((p) => p.id.toString() === postId);
  const { data: comments } = useGetCommentsQuery();
  const [postComments, setPostComments] = useState([]);

  // /    
  const [isFavorite, setIsFavorite] = useState(false);
  const dispatch = useDispatch();

  // useEffect(() => {
  //   //    
  //   if (comments && comments.users) {
  //     const filteredComments = comments.filter((comment) => {
  //       // ,      userId,  id 
  //       return (
  //         comment.userId &&
  //         comments.users.find((user) => user.id === comment.userId)
  //       );
  //     });

  //     setPostComments(filteredComments);
  //   }
  // }, [comments]);

  //    
  const handleAddToFavorites = () => {
    dispatch(addToFavorites(post.id));
    setIsFavorite(true);
  };

  //    
  const handleRemoveFromFavorites = () => {
    dispatch(removeFromFavorites(post.id));
    setIsFavorite(false);
  };

  //   
  const checkIsFavorite = () => {
    //  -   
    if (isFavorite) {
      handleRemoveFromFavorites();
    } else {
      handleAddToFavorites();
    }
  };

  //     
  useEffect(() => {
    if (comments && post) {
      setPostComments(
        comments.filter((comment) => +comment.postId === post.id),
      );
    }
  }, [comments, post]);

  //   
  function handleNewComment(commentText) {
    const newComment = {
      postId,
      text: commentText,
    };
    setPostComments([...postComments, newComment]);
  }

  if (!post) {
    return <div>Post not found</div>;
  }

  //  
  const date = new Date();
  const formattedDate = `${date.getDate()}.${date.getMonth() + 1}.${date.getFullYear()}`;

  return (
    <div className={s.post}>
      <Link to="/posts">
        <Button
          className={s.btnr}
          variant="contained"
          color="primary"
          size="small"
        >
          
        </Button>
      </Link>
      <h1 className={s.post__title}>{post.title}</h1>
      <div className={s.post__div}>
        <p className={s.post__data}>
          {new Date(post.date).toLocaleDateString()}
        </p>
        <p className={s.post__kat}>{post.subtitle}</p>

        <img className={s.post__img} src={post.img.img1x} alt={post.title} />

        <p className={s.post__text}>{post.text}</p>
      </div>
      <div className={s.post__btn}>
        <p className={s.post__username}>{post.author}</p>

        <div className={s.post__choice}>
          <FavoriteBorderIcon
            onClick={checkIsFavorite}
            className={s.like}
            color="primary"
          />
        </div>
      </div>

      {/*    */}
      <AddComment onCommentAdded={handleNewComment} postId={postId} />

      {/*   */}
      <ul className={s.commList}>
        {postComments.map((comment) => (
          <li className={s.commentLi} key={comment.id}>
            <p> </p>
            <p className={s.commentDate}>{formattedDate}</p>
            {comment.text}
          </li>
        ))}
      </ul>
    </div>
  );
};
